import React,{Component} from 'react';
import $ from 'jquery';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css"; 
import tilteObj from './../../images/titleObjetivo.svg';
import data from '../../images/obj/slider.js';
import {Container,Title,Carrossel,Item} from './styled';
import prod from "../../produtos";

class Objetivo extends Component{
  constructor(props){
    super(props);
    this.state={
      subTitle:"Conheça os nossos Serviços",
      pos:data.length,
      response:3,

    };

    
    this.roleta=[];
    this.imgs=[]

    data.map(item=>this.imgs.push(item.image));

    data.map(item=>this.roleta.push(item.title));
    
    this.state.response = this.roleta.length;

    this.click = this.click.bind(this);
    this.custom = this.custom.bind(this);

    this.faciais =prod.face;
    this.custom();
   
  }



  render(){
    const settings = {
      infinite: true,
      // lazyLoad: true,
      speed: 500,
      autoplaySpeed: 5000,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      dots: true,
      dotsClass:"dots",
      appendDots: dots => (
        <ul style={{padding:"5px", display:"flex" }}> {dots} </ul>
      ),
      mobileFirst:true,
      customPaging: i => (
        <button>
        </button>
      ),
      responsive: [
        {
          breakpoint: 1800,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
          }
        },
        {
          breakpoint: 1000,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
          }
        },
        {
          breakpoint: 800,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }],
        arrow:false,
    
    };

    return (
      <Container className="Objetivo">
          <Title>
              <img src={tilteObj}  alt=""/>
          </Title>

          <Carrossel className="carrousel">
            <Slider ref={slider => (this.slider = slider)} {...settings} className="roda">
              {this.roleta.map((item,i)=>{
                let index =i;
                if(index>=this.roleta.length)
                  index-=this.roleta.length;

                return <Item back={this.imgs[index]} className="item" key={i}> <span>{item}</span> </Item>;
              })}
            </Slider>
          </Carrossel>
          
      </Container>
    );
  }

  componentDidMount(){
    this.custom();
  }

  componentDidUpdate(){
    this.custom();
    
  }

  click(e){
    let pos =$(e.target).index();
    this.slider.slickGoTo(pos);
    
    e.preventDefault();
  }

  custom(){
    $(".dots li").each((index)=>{
      $(".dots li").eq(index).click(this.click);
    });

    window.setInterval(()=>{ 
      $(".dots li").css("backgroundColor","var(--primaryColor)");
      $(".dots li.slick-active").css("backgroundColor","#fff"); 
      $(".item").css({
        display:"flex",
      });
    }, 1);


    // Customização do Carrosel
    $(".roda").css({
      display:"grid",
      gridTemplateColumns:"auto 1fr auto",
      overflow:"hidden"
    });

    $(".slick-list").css({
      width:"100%"
    });
    
    $("ul.dots").css({
      gridColumn:"span 3"
    });    
      
    

  }
  
  
  
}

export default Objetivo;
