import React,{Component} from 'react';
import {Container,Title,Categorias,Item} from './styled';
import tilteCat from '../../images/titleCategoria.svg';
import $ from 'jquery';
import {Corporal,Facial,Massagem} from '../../images/categoria/slider';

class Categoria extends Component {
  
  render(){
    return (
      <Container className="Categoria">
          <Title>
            <img src={tilteCat}  alt=""/>
          </Title>

          <Categorias>
            <Item className="item" href="#/produto/faciais" image={Facial} text="tratamentos faciais" />
            <Item className="item" href="#/produto/corporais" image={Corporal} text="tratamentos corporais" />
            <Item className="item" href="#/produto/massagens" image={Massagem} text="massagens" />
          </Categorias>       
      </Container>
    );
  }


  componentDidMount(){
    $(document).ready(function () {
      $(".item").each(i=>{
        $(".item").eq(i).height($(".item").eq(i).width());
      });
    });
    $(window).resize(function () { 
      $(".item").each(i=>{
        $(".item").eq(i).height($(".item").eq(i).width());
      });
      
    });
  }
}

export default Categoria;
