import styled from 'styled-components';


export const Container = styled.header`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    min-height:auto;

    *:focus{
        outline:none;
        box-sizing:border-box;
    }
    @media screen and (max-width: 1024px) {
        min-height:auto;
    }

`;

export const Title = styled.div`
    

    img{
        padding:5px 10px;
        margin-top:10px;
        min-height:150px;
        height:120%;
        max-height:25vh;
        width:100%;
        z-index:3;
        border-radius: 0px 0px 10px 10px;
    }
    display:flex;
    flex-direction:column;
    position:absolute;
    top:80px;
    left:auto;
    right:auto;

`;
export const Carrossel = styled.div`
    overflow:hidden;
    width:100%;
    height:100%;
    margin-bottom:30px;
    display:"flex";
    flex-direction:column;
    justify-content:center;

    .dots{
        width:100%;
        display:flex;
        justify-content:center;

        *{overflow:hidden};

        li{
            background-color:var(--primaryColor);
            cursor:pointer;
            width:20px;
            height:20px;   
            border-radius:100%;   
            margin:0px 5px;
            
        }
    }
    .roda{
        *{
            overflow:hidden;
        }
        .slick-arrow{
            display:flex;
            align-items:center;
            justify-content:center;
            background-color:var(--primaryColor);
            color:var(--backColor);
            font-size:30px;
            font-weight:1000;
            cursor: pointer;
        }
        .slick-prev{
            transform: rotateY(180deg);
        }
        

    }

    
`;

export const Item = styled.div`
    min-height:600px;
    max-height:600px;
    display:flex;
    justify-content:center;
    align-items:flex-end;
    z-index:10;
    background-image: url(${props=>props.back});
    background-position:center;
    background-repeat:no-repeat;
    cursor: pointer;
    overflow:hidden;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;

    
    *{
        box-sizing:border-box;
    }

    span{
        background-color:var(--primaryColor);
        padding:8px;
        font-weight:bolder;
        text-transform:uppercase;
        font-size:1.2em;
        color:var(--backColor);
        box-shadow:0 0 10px var(--backColor);
        
    }

`;