import styled from 'styled-components';


export const Container = styled.header`
    
    background-size:cover;
    background-attachment:fixed;
    width:100%;
    min-height:85vh;
    height:min-content;
    position:relative;    
    background-color:rgba(27,27,27);  
    
    display:grid;
    padding:15px 5vw;
    z-index: 2; 
    justify-items:center;
    align-items:center;
    grid-template-rows:repeat(4,auto);
    row-gap:15px;

    :after {
        content: "";
        background-image:url( ${props => props.image ? props.image: "" });
        background-position:center;
        background-size:cover;
        background-attachment:fixed;
        opacity: .3;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: -1;   
    }

    .title{
        font-size:1.8em;
        color:var(--primaryColor);
        text-align:center;

        h3{
            color:white;
            font-size:.8em;
            margin-left: 10vw;
            text-align:center;
        }
    }
    *{
        overflow:hidden;
    }
    .desc{
        font-size:1.3em;
        width:95%;
        max-width:1000px;
        text-align:justify;
        line-height: 150%;
        
        /* word-break: break-all;  */
    }

    
    button{
        background-color:#2f8c3c;
        box-shadow:inset 0px -3px rgba(27,27,27,.3);
        height:fit-content;
        padding:15px;
        color:white;
        font-weight:bold;
        border-radius: 50px;
        transition:.3s ease-in;
        font-size:1.5em;
        :hover{
            transform:translateY(-8px);
            box-shadow:inset 0px -5px rgba(27,27,27,.3);
        }
    }

    ul{
        li{
            font-size:1.2em;
            font-weight:bold;
            list-style-type: circle;
            :before{
                content:"•";
                margin-right:5px;
            }
        }
        
    }


    @media screen and (max-width: 1024px) {
        .desc{
            font-size:20px;
            text-align:center;
        }
    }
`;