import React,{Component} from 'react';

import todabella from '../../images/todabella.jpg';

import {Container} from './styled';
import $ from 'jquery';

class Metodo extends Component{
    constructor(props){
        super(props);
        this.sendMessage = this.sendMessage.bind(this);
    }

  render(){
    return (
      <Container className="Metodo" image={todabella}>

            <div className="title"><h1>Método Todabella</h1></div>
            
            <div className="desc">
                <h3>O Método Todabella constitui-se na associação de técnicas e terapias eletroterapêuticas, cosmecêuticas. Onde atuamos diretamente sobre a queixa da cliente. Porque não existe receita de bolo, por isso montamos protocolo individual mediante avaliação.</h3> 
            </div>

            <div className="title"><h2>Com a nossa proposta de tratamento estético conseguimos:</h2></div>
            <ul className="desc">
              <li>Retrair a pele, estimular a neocolagenese ( novo colágeno)</li>
              <li>Compactar tecido adiposo, reduzindo ( gordura)</li>
              <li>Diminuir a diástase ( afastamento das fibras musculares)</li>
              <li>Remodelar a silhueta </li>
            </ul>


            <div className="title"><h2>Entenda como funciona:</h2></div>
            <ul className="desc">
              <li>O Método Todabella consiste na associação de técnicas e terapias eletroterapêuticas, cosmecêuticas e manuais. </li>
              <li>Utilizamos a mais alta tecnologia e associamos a técnicas reconhecidas e comprovadas no mercado da estética. Onde atuamos diretamente sobre a queixa da cliente. Porque não existe receita de bolo,  por isso montamos protocolos individual mediante avaliação. </li>
              <li>Para obtenção de resultados satisfatórios, é necessário o cumprimento do protocolo proposto, assim como, a continuidade, frequência e  respeito quanto o intervalo das sessões. </li>
              <li>Remodelar a silhueta </li>
            </ul>


            <div className="title"><h2>O tratamento consiste em 08 sessões onde trabalhamos 3 fases distintas:</h2></div>
            <ul className="desc">
              <li><b>1) FASE DE ATAQUE</b> - desintoxicação celular </li>
              <li><b>2) FASE DE REMODELAÇÃO</b> - onde conseguimos realizar a modelagem corporal, diminuindo  tecido de gordura localizada e retenção de líquidos além da renovação de um novo colágeno.</li>
              <li><b>3) FASE DE MANUTENÇÃO</b> - a fase mais importante para mantermos o resultado obtido na fase anterior.</li>
            </ul>

            <div className="title">
                <h2>Ficou Curioso ?</h2>
                <h3>Marque a sua avaliação !</h3>
            </div>
            <button onClick={this.sendMessage}>Faça a sua avaliação!</button>

      </Container>
    );
  }

  sendMessage(){
    let texto="";
    


    let conversa = encodeURI("Olá, prazer eu estava olhando seu Método Todabella, pelo site Todabella Manaus e me interessei em realizar uma *Avaliação*.");
    let _url="";
    if (/Mobi/.test(navigator.userAgent)) {
        _url="https://api.whatsapp.com/send?phone=5592999039215&text="+conversa;
    }
    else{
        _url="https://web.whatsapp.com/send?phone=5592999039215&text="+conversa;
    }

    console.log(_url);
    window.open(_url, '_blank');

}
}

export default Metodo;
