import React from 'react';
import {Container} from './styled';

import Objetivo from '../../Components/Objetivo';
import Categoria from '../../Components/Categoria';
import Metodo from '../../Components/Metodo';

function Home() {
  return (
    <Container className="Home">
        <Objetivo></Objetivo>
        <Categoria></Categoria>
        <Metodo></Metodo>
    </Container>
  );
}

export default Home;
